import React, { useEffect, useState } from 'react';
import {NavLink, useParams} from 'react-router-dom';
import { getUserRatingCount } from '../../API/UserApi';
import NotFound from '../NotFound/NotFound';
import "./UserRatingPage.css"
import CollectionCard from "../../components/CollectionCard/CollectionCard";

function NavKink() {
    return null;
}

function UserRatingPage({ userInfo, openLogin }) {
    const [notFound, setNotFound] = useState(false);

    const { id } = useParams();

    const [userRatingCount, setUserRatingCount] = useState(null);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const res = await getUserRatingCount(id);
                setUserRatingCount(res.data);
            } catch (error) {
                setNotFound(true);
            }
        };
        fetchData();
    }, [id]);

    useEffect(() => {
        console.log(userRatingCount);
    }, [userRatingCount]);

    return (notFound ? <NotFound /> :
        <div className="user-rating-page container">
            <div className="user-rating-page-wrap">
                <h1></h1>
                <ul className="rating-list">
                    <li><NavLink to={`/user/${id}/content/movie/rating`}> <span>{userRatingCount?.movie}</span></NavLink></li>
                    <li><NavLink to={`/user/${id}/content/series/rating`}> <span>{userRatingCount?.series}</span></NavLink></li>
                    <li><NavLink to={`/user/${id}/content/book/rating`}> <span>{userRatingCount?.book}</span></NavLink></li>
                    <li><NavLink to={`/user/${id}/content/webtoon/rating`}> <span>{userRatingCount?.webtoon}</span></NavLink></li>
                    <li><NavLink to={`/user/${id}/content/record/rating`}> <span>{userRatingCount?.record}</span></NavLink></li>
                </ul>
            </div>
        </div>
    )
}

export default UserRatingPage;