import React, { useEffect, useState } from 'react';
import {Link, NavLink, useLocation, useNavigate} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import './Header.css';
import {
    checkEmail,
    checkVerification,
    createVerification,
    link,
    registerUser,
    setPassword
} from '../../API/UserApi';
import { toast } from "react-toastify";

const Header = ({ userInfo, handleLogin, handleLogout, isLoginOpen, openLogin, closeLogin }) => {
    /*      */
    const location = useLocation();
    const [scrolled, setScrolled] = useState(false);
    const isDetailPage = location.pathname.match(/^\/content\/\d+/);

    useEffect(() => {
        const handleScroll = () => setScrolled(window.scrollY > 50);

        if (isDetailPage) {
            window.addEventListener('scroll', handleScroll);

            /*      scrolled */
            if (location.pathname.includes('/comment')) setScrolled(true);

            return () => window.removeEventListener('scroll', handleScroll);
        } else {
            setScrolled(true);
        }
    }, [isDetailPage, location.pathname]);

    /*      */
    const [loginEmail, setLoginEmail] = useState('');
    const [loginPassword, setLoginPassword] = useState('');
    const [loginError, setLoginError] = useState(null);
    const [isErrorModalOpen, setErrorModalOpen] = useState(false);
    const closeErrorModal = () => setErrorModalOpen(false);

    /*    */
    const [verifyingEmail, setVerifyingEmail] = useState('');
    const [isSignupOpen, setSignupOpen] = useState(false);
    const [signupNickname, setSignupNickname] = useState('');
    const [signupEmail, setSignupEmail] = useState('');
    const [signupPassword, setSignupPassword] = useState('');

    const [isResetOpen, setResetOpen] = useState(false);
    const [isResetCheckOpen, setResetCheckOpen] = useState(false);
    const [isResetNewOpen, setResetNewOpen] = useState(false);

    /*    */
    const handleCreateVerification = async (email) => {
        try {
            await checkEmail({ email });
            setVerifyingEmail(email);
            toast('  .  .');
            createVerification({ email });
            closeReset();
            openResetCheck();
        } catch {
            toast('   .  .');
        }
    };

    const handleCheckVerification = async (email, code) => {
        try {
            await checkVerification({ email, code });
            toast(' .  .');
            closeResetCheck();
            openResetNew();
        } catch {
            toast('  .  .');
        }
    };

    const handleSetPassword = async (newPassword) => {
        try {
            await setPassword({ email: verifyingEmail, newPassword });
            toast('  .');
            closeResetNew();
            openLogin();
        } catch {
            toast('  .  .');
        }
    };

    /*   */
    const handleSignup = async () => {
        try {
            await registerUser({
                nickname: signupNickname,
                email: signupEmail,
                password: signupPassword
            });
            toast(' .');
            closeSignup();
        } catch {
            toast('  .  .');
        }
    };

    /*  /  */
    const openSignup = () => setSignupOpen(true);
    const closeSignup = () => setSignupOpen(false);

    const openReset = () => setResetOpen(true);
    const closeReset = () => setResetOpen(false);

    const openResetCheck = () => setResetCheckOpen(true);
    const closeResetCheck = () => setResetCheckOpen(false);

    const openResetNew = () => setResetNewOpen(true);
    const closeResetNew = () => setResetNewOpen(false);

    /* */
    const navigate = useNavigate(); // useNavigate  

    const [searchKeyword, setSearchKeyword] = useState(""); //   

    //  
    const handleSearchSubmit = (e) => {
        e.preventDefault();
        if (!searchKeyword.trim()) return;

        // HashRouter  URL 
        navigate(`/search?keyword=${encodeURIComponent(searchKeyword)}`);
       /* setSearchKeyword(""); //    */
    };

    return (
        <>
            {/*  */}
            <header className={`header ${scrolled ? 'header-scrolled' : 'header-transparent'}`}>
                <div className="header_width container">

                    {/* :    */}
                    <div className="header_inner header_left">
                        <NavLink to="/" className="logo">
                            <img src={`${process.env.PUBLIC_URL}/itview-logo/mainLogo.svg`} alt=""/>
                        </NavLink>

                        {/*  */}
                        <ul className="nav">
                            <li><NavLink to="/"></NavLink></li>
                            <li><NavLink to="/movies"></NavLink></li>
                            <li><NavLink to="/series"></NavLink></li>
                            <li><NavLink to="/books"></NavLink></li>
                            <li><NavLink to="/webtoons"></NavLink></li>
                            <li><NavLink to="/music"></NavLink></li>
                        </ul>

                        {/* /  */}
                        <div className="nav-item dropdown">
                            <NavLink to="/" className="nav-link dropdown-toggle" role="button" data-bs-toggle="dropdown"
                                     aria-expanded="false"></NavLink>
                            <ul className="dropdown-menu">
                                <li><NavLink to="/movies"></NavLink></li>
                                <li><NavLink to="/series"></NavLink></li>
                                <li><NavLink to="/books"></NavLink></li>
                                <li><NavLink to="/webtoons"></NavLink></li>
                                <li><NavLink to="/music"></NavLink></li>
                            </ul>
                        </div>
                    </div>

                    {/* :   / */}
                    <div className="header_inner">

                        {/*  */}
                        {/*  */}
                        <div className="search-bar">
                            <form onSubmit={handleSearchSubmit}>
                                <button type="submit" className="search-button">
                                    <i className="bi bi-search" />
                                </button>
                                <input
                                    type="text"
                                    placeholder=" "
                                    className="search-input"
                                    value={searchKeyword}
                                    onChange={(e) => setSearchKeyword(e.target.value)}
                                />
                            </form>
                        </div>

                        {/* / */}
                        {userInfo ? (
                            <div className="user-menu">
                                <Link to={`/user/${userInfo}`} className="login-button"></Link>
                                <button onClick={handleLogout} className="login-button"></button>
                            </div>
                        ) : (
                            <div className="user-menu">
                                <button onClick={openLogin} className="login-button"></button>
                                <button onClick={openSignup} className="signUp-button"></button>
                            </div>
                        )}
                    </div>
                </div>
            </header>

            {/*   */}
            {isLoginOpen && (
                <div className="modal-overlay" onClick={closeLogin}>
                    <div className="login-modal" onClick={(e) => e.stopPropagation()}>
                        <h1><img src={`${process.env.PUBLIC_URL}/itview-logo/mainLogo.svg`} alt=""/></h1>
                        <h2></h2>
                        <form onSubmit={async (e) => {
                            e.preventDefault();
                            const res = await handleLogin(loginEmail, loginPassword);
                            if (res) {
                                closeLogin();
                                window.location.reload();
                            } else {
                                setLoginError(' .  .');
                                setErrorModalOpen(true);
                            }
                        }}>
                            <input type="email" placeholder="" onChange={(e) => setLoginEmail(e.target.value)}/>
                            <input type="password" placeholder="" onChange={(e) => setLoginPassword(e.target.value)}/>
                            <button type="submit" className="login-submit"></button>
                        </form>
                        <p onClick={openReset} className="login-text-top"> ?</p>
                        <p className="login-text-bottom">
                             ?
                            <span onClick={() => {closeLogin(); openSignup();}}></span>
                        </p>
                        {/*   */}
                        <div className="login-social">
                            <div className="login-social-border">OR</div>
                            <div className="login-social-logo">
                                <a href={`${process.env.REACT_APP_API_URL}/oauth2/authorization/kakao`}
                                   onClick={() => link({redirectURL: window.location.href})}><img src={`${process.env.PUBLIC_URL}/LoginLogo/kakao-logo.svg`} alt=""/></a>
                                <a href={`${process.env.REACT_APP_API_URL}/oauth2/authorization/naver`}
                                   onClick={() => link({redirectURL: window.location.href})}><img src={`${process.env.PUBLIC_URL}/LoginLogo/naver-logo.svg`} alt=""/></a>
                                <a href={`${process.env.REACT_APP_API_URL}/oauth2/authorization/google`}
                                   onClick={() => link({redirectURL: window.location.href})}><img src={`${process.env.PUBLIC_URL}/LoginLogo/google-logo.svg`} alt=""/></a>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            {/*   */}
            {isSignupOpen && (
                <div className="modal-overlay" onClick={closeSignup}>
                    <div className="login-modal" onClick={(e) => e.stopPropagation()}>
                        <h1><img src={`${process.env.PUBLIC_URL}/itview-logo/mainLogo.svg`} alt=""/></h1>
                        <h2></h2>
                        <form onSubmit={(e) => { e.preventDefault(); handleSignup(); }}>
                            <input type="text" placeholder="" onChange={(e) => setSignupNickname(e.target.value)}/>
                            <input type="email" placeholder="" onChange={(e) => setSignupEmail(e.target.value)}/>
                            <input type="password" placeholder="" onChange={(e) => setSignupPassword(e.target.value)}/>
                            <button type="submit" className="login-submit"></button>
                        </form>
                        <p className="login-text-bottom">
                             ?
                            <span onClick={() => {closeSignup();openLogin();}}></span>
                        </p>
                        {/*   */}
                        <div className="login-social">
                            <div className="login-social-border">OR</div>
                            <div className="login-social-logo">
                                <a href={`${process.env.REACT_APP_API_URL}/oauth2/authorization/kakao`}
                                   onClick={() => link({redirectURL: window.location.href})}><img src={`${process.env.PUBLIC_URL}/LoginLogo/kakao-logo.svg`} alt=""/></a>
                                <a href={`${process.env.REACT_APP_API_URL}/oauth2/authorization/naver`}
                                   onClick={() => link({redirectURL: window.location.href})}><img src={`${process.env.PUBLIC_URL}/LoginLogo/naver-logo.svg`} alt=""/></a>
                                <a href={`${process.env.REACT_APP_API_URL}/oauth2/authorization/google`}
                                   onClick={() => link({redirectURL: window.location.href})}><img src={`${process.env.PUBLIC_URL}/LoginLogo/google-logo.svg`} alt=""/></a>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            {/*    */}
            {isErrorModalOpen && (
                <div className="modal-overlay" onClick={closeErrorModal}>
                    <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                        <p>{loginError}</p>
                        <button onClick={closeErrorModal}></button>
                    </div>
                </div>
            )}

            {/*    */}
            {isResetOpen && (
                <div className="modal-overlay" onClick={closeReset}>
                    <div className="reset-modal" onClick={(e) => e.stopPropagation()}>
                        <button className="close-button" onClick={closeReset}><i className="bi bi-x-lg"/></button>
                        <h2> </h2>
                        <p>  .</p>
                        <form onSubmit={(e) => {
                            e.preventDefault();
                            const email = e.target[0].value;
                            if (email) handleCreateVerification(email);
                        }}>
                            <input type="email" placeholder=""/>
                            <button type="submit" className="reset-submit"> </button>
                        </form>
                    </div>
                </div>
            )}

            {/*    */}
            {isResetCheckOpen && (
                <div className="modal-overlay" onClick={closeResetCheck}>
                    <div className="reset-modal" onClick={(e) => e.stopPropagation()}>
                        <button className="close-button" onClick={closeResetCheck}><i className="bi bi-x-lg"/></button>
                        <h2> </h2>
                        <p> </p>
                        <form onSubmit={(e) => {
                            e.preventDefault();
                            const code = e.target[0].value;
                            if (code) handleCheckVerification(verifyingEmail, code);
                        }}>
                            <input type="text" placeholder=""/>
                            <button type="submit" className="reset-submit"></button>
                        </form>
                    </div>
                </div>
            )}

            {/*     */}
            {isResetNewOpen && (
                <div className="modal-overlay" onClick={closeResetNew}>
                    <div className="reset-modal" onClick={(e) => e.stopPropagation()}>
                        <button className="close-button" onClick={closeResetNew}><i className="bi bi-x-lg"/></button>
                        <h2> </h2>
                        <p>  </p>
                        <form onSubmit={(e) => {
                            e.preventDefault();
                            const newPassword = e.target[0].value;
                            if (newPassword) handleSetPassword(newPassword);
                        }}>
                            <input type="password" placeholder=""/>
                            <button type="submit" className="login-submit"> </button>
                        </form>
                    </div>
                </div>
            )}
        </>
    );
};

export default Header;
