import React, { useEffect, useState } from 'react';
import { BrowserRouter, HashRouter } from 'react-router-dom';
import Header from './components/Header/Header';
import AppRoutes from './routes/AppRoutes';
import "./App.css"
import { getMyInfo, loginUser, logoutUser } from './API/UserApi';
import { ToastContainer, Slide, toast } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';

function App() {

    //   
    const [userInfo, setUserInfo] = useState(null);

    const checkLoginStatus = async () => {
        try {
            const response = await getMyInfo();
            setUserInfo(response.data);
        } catch (error) {
            setUserInfo(null);
        }
    };

    //     FLASH  
    useEffect(() => {
        checkLoginStatus();
        const match = document.cookie.match(/(?:^|;\s*)FLASH_ERROR=([^;]+)/);
        if (!match) return;
        const msg = decodeURIComponent(match[1]);
        if (msg) {
            setTimeout(() => toast(msg), 0); //    alert 
        }

        //     
        document.cookie = "FLASH_ERROR=; Max-Age=0; Path=/";
    }, []);

    useEffect(() => {
        console.log("userId : ", userInfo);
    }, [userInfo]);

    const handleLogin = async (loginEmail, loginPassword) => {
        //   
        try {
            await loginUser({
                email: loginEmail,
                password: loginPassword
            });
            return true;
        } catch (error) {
            return false;
        }
    };

    const handleLogout = async () => {
        //   
        try {
            await logoutUser();
            setUserInfo(null);
            window.location.reload();
        } catch (error) {
            toast(' .  .');
        }
    };

    //   
    const [isLoginOpen, setLoginOpen] = useState(false);
    
    const openLogin = () => setLoginOpen(true);
    const closeLogin = () => setLoginOpen(false);

    return (
        <HashRouter>
            <Header userInfo={userInfo} handleLogin={handleLogin} handleLogout={handleLogout} isLoginOpen={isLoginOpen} openLogin={openLogin} closeLogin={closeLogin}/>
            <AppRoutes userInfo={userInfo} openLogin={openLogin} />

            <ToastContainer
                position="bottom-center"   //  (top-right, top-center, bottom-left )
                autoClose={1000}       //    (ms)
                hideProgressBar={true} //   
                newestOnTop={false}    //    
                closeOnClick           //   
                transition={Slide}
                pauseOnHover           //   
                draggable              //  
                icon={false}
            />
        </HashRouter>
    );
}
export default App;
